/*=============================
  Base Styles
=============================*/
* {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
}
body {
  line-height: 1.6;
  color: #2b2b2b; /* Changed color from original */
  font-size: 16px;
  font-weight: 400;
  font-family: "Work Sans", sans-serif; /* Changed font from original */
}

@media screen and (max-width: 768px) {
  body {
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  body {
    font-size: 12px;
  }
}

a {
  color: #81d4fa;
  /* color: #e1f5fe; */
}

/*=============================
  Grid
=============================*/
.app-container-day {
  background-color: #fffafa;
  height: 100vh;
  overflow: auto;
  width: 100%;
}

.app-container-night {
  /* background-color: #37474f; */
  background-color: #191919;
  height: 100vh;
  overflow: auto;
  width: 100%;
}

.container {
  width: 80%;
  max-width: 960px;
  margin: 0 auto;
}

@media screen and (max-width: 600px) {
